@font-face {
  src: url("../fonts/Atenta-Regular.woff2") format("woff2");
  font-family: "Atenta";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 600;
  color: #000000;
  background-color: #020A18;
}

.site-container {
  overflow: hidden;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  max-width: 1165px;
  padding: 0 15px;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  border: none;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.header {
  margin-bottom: 180px;
  width: 100%;
  background-color: #020A18;
}

.header.fixed {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
}

.header__container {
  max-width: 1165px;
  margin: 0px auto;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  min-height: 70px;
}

.header__logo {
  z-index: 5;
  display: block;
  margin-right: 85px;
}

.header__link {
  padding: 15px 30px;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: #3A68B8;
  border-radius: 5px;
  border: 3px solid;
  border-color: transparent;
  transition: .2s;
}

.header__link:hover {
  background-color: #020A18;
  border-color: #3A68B8;
  color: #3A68B8;
}

.logo__img {
  width: 283px;
  height: 57px;
  fill: #ffffff;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.menu__burger {
  display: none;
}

.menu__item {
  margin-right: 40px;
}

.menu__item:last-child {
  margin: 0;
}

.menu__link {
  position: relative;
  color: #ffffff;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  transition: .1s;
}

.menu__link:hover {
  opacity: 0.8;
}

.menu__link::after {
  position: absolute;
  content: "";
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  visibility: hidden;
  transition: width .5s;
}

.menu__link:hover::after {
  width: 100%;
  opacity: 0.8;
  visibility: visible;
}

@media (min-width: 767px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
  .menu__item {
    padding: 10px 0;
  }
}

.reviews {
  margin-bottom: 160px;
}

.reviews__container {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1190px;
}

.reviews__wrap {
  max-width: 560px;
}

.reviews-wrap__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: 'Open Sans';
  font-weight: 700;
  font-size: 40px;
  color: #ffffff;
}

.reviews-wrap__text {
  margin: 0;
  margin-bottom: 20px;
  max-width: 470px;
  font-family: 'Open Sans';
  font-weight: 600;
  font-size: 16px;
  color: #ffffff;
  opacity: 0.7;
}

.reviews-wrap__text:last-child {
  margin: 0;
}

.footer {
  padding-top: 110px;
  margin-bottom: 140px;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 815px;
}

.footer-menu__title {
  margin: 0;
  margin-bottom: 30px;
  font-family: 'Circular Std';
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

.footer-menu__link {
  display: block;
  margin-bottom: 20px;
}

.footer-menu__link:last-child {
  margin: 0;
}

.footer-menu__item {
  margin: 0;
  font-family: 'Circular Std';
  font-weight: 450;
  font-size: 14px;
  color: #ffffff;
  opacity: 0.6;
  transition: .2s;
}

.footer-menu__item:hover {
  opacity: 1;
}
